html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a:hover {
  text-decoration: none;
}

ins, a {
  text-decoration: none;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  outline: 0;
}

input:focus, select:focus, textarea:focus, button:focus {
  outline: 0;
}

[contenteditable=true] {
  padding: 0;
  margin: 0;
  text-decoration: none;
  outline: none;
}

body .wrap {
  position: relative;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

body .wrap .nav {
  position: fixed;
  display: block;
  top: 0;
  width: 100%;
  height: 46px;
  background-color: #308690;
  z-index: 20;
}

body .wrap .nav a img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 20px;
  width: 160px;
}

body .wrap .container {
  width: 100%;
}

body .wrap .container .section-intro-header {
  position: relative;
  height: 700px;
  background-color: #3eadb9;
}

body .wrap .container .section-intro-header .bg-head {
  position: absolute;
  width: 100%;
  height: inherit;
  overflow: hidden;
  top: 0;
  left: 0;
}

body .wrap .container .section-intro-header .bg-head .bg-landing-download {
  height: 100%;
