}

body .wrap .container .section-intro-header .intro-img-form {
  position: relative;
  width: 100%;
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
}

@keyframes floating {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(3%);
  }
  100% {
    transform: translateY(0%);
  }
}

body .wrap .container .section-intro-header .intro-img-form .intro-img {
  position: absolute;
  height: 455px;
  top: 200px;
  right: 140px;
  animation: floating 2s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

