  position: absolute;
  bottom: 52px;
  left: 437px;
  width: 200px;
  height: 250px;
}

body .wrap .container .section-intro-content {
  width: 100%;
  height: 100%;
  background: url(../img/landingpage-opentest/bg-opentest.jpg) repeat 10%;
  padding: 0 0 48px 0;
}

body .wrap .container .section-intro-content .adjust-flex {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: baseline;
}

body .wrap .container .section-intro-content .adjust-flex .app-intro {
  width: 320px;
  margin: 48px 0px 0px 0px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
