body .wrap .container .section-intro-content .adjust-flex .app-intro:hover {
  box-shadow: 0 0 18px 10px rgba(0, 0, 0, 0.1);
  transform: scale(1.04);
}

body .wrap .footer {
  width: 100%;
  height: 80px;
  font-family: Microsoft JhengHei, sans-serif;
  background-color: #308690;
  text-align: center;
  line-height: 80px;
  color: #ffffff;
  font-size: 18px;
}

